import React from 'react';
import { graphql } from 'gatsby';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query } from '../../graphql-types';
import Product from '../components/product';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Separator } from '../components/separator';
import { Link } from 'gatsby-plugin-intl';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Breadcrumb from 'react-bootstrap/Breadcrumb';

const BrandPage: React.FunctionComponent<{ data: Query }> = ({ data: { allorsBrand } }) => {
  const brand = allorsBrand;
  const seoImage = brand.logoImage?.file?.childImageSharp?.original?.src;
  const products = brand?.partsWhereBrand?.map((v) => v.UnifiedGood);

  return (
    <Layout>
      <SEO title={brand.name} description={brand.seoDescription ?? brand.description} image={seoImage} />
      <main>
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col md="12">
                  <Breadcrumb>
                    <Breadcrumb.Item>
                      <Link to="/">Home</Link>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item active>{brand.name}</Breadcrumb.Item>
                  </Breadcrumb>
                  <h1 className="page-title">{brand.name}</h1>
                  <p className="page-description">{brand.seoDescription}</p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          <Container>
            <Row>
              <Col xl="9" lg="8" className="order-lg-2">
                <Row>
                  {products &&
                    products.map((product) => {
                      return <Product key={product.slug} product={product}></Product>;
                    })}
                </Row>
              </Col>
              <Col xl="3" lg="4" className="order-lg-1">
                <div className="list-group mb-4">
                  <Link to={'/brands'} className="list-group-item list-group-item-action active">
                    <FontAwesomeIcon icon="chevron-left" /> Back
                  </Link>
                </div>
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    allorsBrand(slug: { eq: $slug }) {
      seoDescription
      logoImage {
        file {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      name
      slug
      partsWhereBrand {
        UnifiedGood {
          name
          slug
          primaryPhoto {
            file {
              childImageSharp {
                id
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`;

export default BrandPage;
